.s-requested-info {
    padding: rem(40) 0;

    @media (min-width: 768px) {
        padding: rem(50) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(60) 0;
    }

    @media (min-width: 1600px) {
        padding: rem(75) 0;
    }

    &__title {
        margin-bottom: rem(40);
        font-size: rem(25);
        line-height: 100%;
        color: white;
        letter-spacing: -0.03em;

        @media (min-width: 768px) {
            font-size: rem(35);
            line-height: rem(41);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(50);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(70);
            font-size: rem(38);
            line-height: rem(45);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(80);
            font-size: rem(40);
            line-height: rem(47);
        }
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: rem(40);

        @media (min-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
            gap: rem(75) rem(30);
        }

        @media (min-width: 1024px) {
            gap: rem(50) rem(30);
        }

        @media (min-width: 1600px) {
            grid-template-columns: repeat(4, 1fr);
            gap: rem(80);
        }

        @media (min-width: 1920px) {
            gap: rem(178);
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        height: 100%;
        flex-direction: column;
        gap: rem(15);

        @media (min-width: 1600px) {
            gap: rem(25);
        }

        &-title {
            margin-bottom: rem(6);
            font-size: rem(19);
            font-weight: 600;
            line-height: rem(22);
            color: #ceced0;

            @media (min-width: 768px) {
                margin-bottom: rem(10);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(30);
                font-size: rem(23);
                line-height: rem(26);
            }

            @media (min-width: 1600px) {
                font-size: rem(28);
                line-height: rem(32);
            }

            @media (min-width: 1920px) {
                font-size: rem(30);
                line-height: rem(34);
            }
        }

        &-text {
            font-size: rem(15);
            line-height: rem(18);
            color: #4e5056;

            @media (min-width: 1024px) {
                font-size: rem(17);
                line-height: rem(20);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
                line-height: rem(22);
            }
        }

        &-bottom {
            padding-top: rem(6);
            border-top: rem(1) solid #656669;

            @media (min-width: 768px) {
                padding-top: rem(10);
            }
        }

        &-link {
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: rem(15);
            line-height: rem(18);
            color: #424346;
            transition: 0.5s;

            @media (min-width: 1600px) {
                font-size: rem(16);
                line-height: rem(19);
            }

            &:hover {
                color: #d91745;

                svg {
                    path {
                        stroke: #d91745;
                    }
                }
            }
        }
    }
}
