.s-support-contact {
    position: relative;
    padding: 38px 0 190px;
    overflow: visible;

    @media (max-width: 1699px) {
        padding: 35px 0 170px;
    }

    @media (max-width: 1199px) {
        padding: 30px 0 110px;
    }

    @media (max-width: 991px) {
        padding: 20px 0 104px;
    }

    @media (max-width: 600px) {
        padding: 47px 0 44px;
    }


    &__title {
        max-width: 821px;
        font-size: 30px;
        line-height: 120%;
        color: #4E5056;
        margin-bottom: 132px;

        @media (max-width: 1699px) {
            max-width: 660px;
            font-size: 25px;
        }

        @media (max-width: 1199px) {
            max-width: 503px;
            font-size: 19px;
            margin-bottom: 70px;
        }

        @media (max-width: 991px) {
            max-width: 403px;
            font-size: 15px;
            margin-bottom: 57px;
        }

        @media (max-width: 600px) {
            font-size: 13px;
            margin-bottom: rem(40);
        }
    }

    &__caption {
        position: absolute;
        top: -250px;
        right: -45%;
        font-size: 350px;
        font-weight: 900;
        letter-spacing: -0.03em;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke: 0.25rem rgba(255, 255, 255, 0.05);
        letter-spacing: -0.03em;

        @media (max-width: 1699px) {
            right: -50%;
            font-size: 320px;
        }

        @media (max-width: 1199px) {
            top: -100px;
            right: -40%;
            font-size: 170px;
        }

        @media (max-width: 991px) {
            right: -42%;
            font-size: 130px;
        }

        @media (max-width: 600px) {
            right: unset;
            left: 0;
            top: -140px;
        }
    }

    &__descriptions {
        display: flex;
        margin-bottom: rem(40);

        @media (max-width: 1499px) {
            align-items: center;
            margin-bottom: 64px;
        }

        @media (max-width: 991px) {
            margin-bottom: 60px;
        }

        @media (max-width: 600px) {
            flex-direction: column;
            margin-bottom: 37px;
        }
    }

    &__subtitle {
        min-width: 889px;
        font-size: 21px;
        line-height: 120%;
        color: #CECED0;

        @media (max-width: 1699px) {
            min-width: 639px;
        }

        @media (max-width: 1499px) {
            font-size: 19px;
            min-width: 0;
        }

        @media (max-width: 991px) {
            font-size: 17px;
        }

        @media (max-width: 600px) {
            font-size: 15px;
            margin-bottom: 15px;
        }
    }

    &__description {
        max-width: 720px;
        font-size: 16px;
        line-height: 120%;
        color: #4E5056;
        margin-left: 120px;

        @media (max-width: 1699px) {
            margin-left: 94px;
        }

        @media (max-width: 1499px) {
            font-size: 15px;
            margin-left: 76px;
        }

        @media (max-width: 991px) {
            font-size: 13px;
            margin-left: 30px;
        }

        @media (max-width: 600px) {
            font-size: 12px;
            margin-left: 0;
        }
    }

    &-legal {
        .s-support-contact {
            &__title {
                margin-bottom: 63px;

                @media (max-width: 1699px) {
                    margin-bottom: 134px;
                }

                @media (max-width: 1199px) {
                    margin-bottom: 21px;
                }

                @media (max-width: 991px) {
                    margin-bottom: 38px;
                }

                @media (max-width: 600px) {
                    margin-bottom: 30px;
                }
            }
        }
    }
}
