.s-reviews {
    padding-top: 183px;
    padding-bottom: 139px;
    overflow: hidden;

    @media (max-width: 1699px) {
        padding-top: 168px;
        padding-bottom: 130px;
    }

    @media (max-width: 1199px) {
        padding-top: 119px;
        padding-bottom: 85px;
    }

    @media (max-width: 991px) {
        padding-top: 109px;
        padding-bottom: 90px;
    }

    @media (max-width: 600px) {
        padding-top: 113px;
        padding-bottom: 85px;
    }

    &__top {
        position: relative;

        @media (max-width: 1199px) {
            margin-bottom: 58px;
        }

        @media (max-width: 991px) {
            margin-bottom: 31px;
        }

        @media (max-width: 600px) {
            margin-bottom: 35px;
        }
    }

    &__caption {
        position: absolute;
        top: -80px;
        right: 0;
        z-index: -1;
        font-size: 350px;
        font-weight: 600;
        line-height: 90%;
        letter-spacing: -0.05em;
        white-space: nowrap;
        color: #000203;
        opacity: 0.5;

        @media (max-width: 1699px) {
            font-size: 300px;
        }

        @media (max-width: 1199px) {
            right: -40px;
            font-size: 200px;
        }

        @media (max-width: 991px) {
            top: -60px;
            right: 0;
            font-size: 150px;
        }

        @media (max-width: 600px) {
            top: -60px;
            right: rem(50);
            font-size: 70px;
        }
    }

    .form {
        position: relative;

        &__title {
            font-size: rem(40);
            line-height: 120%;
            margin-bottom: 20px;

            @media (max-width: 991px) {
                font-size: 35px;
            }

            @media (max-width: 600px) {
                font-size: 23px;
            }
        }

        &__subtitle {
            font-size: 19px;
            line-height: 120%;
            margin-bottom: 14px;
            white-space: nowrap;

            @media (max-width: 1199px) {
                font-size: 15px;
                margin-bottom: 11px;
            }

            @media (max-width: 991px) {
                white-space: pre-wrap;
            }

            @media (max-width: 600px) {
                margin-bottom: 8px;
            }
        }

        &__select {
            margin-bottom: 45px;

            @media (max-width: 1699px) {
            }

            @media (max-width: 1199px) {
                margin-bottom: 35px;
            }

            @media (max-width: 991px) {
                margin-bottom: 43px;
            }

            @media (max-width: 600px) {
                margin-bottom: 25px;
            }
        }

        &__raitings {
            margin-bottom: rem(40);

            @media (max-width: 1699px) {
            }

            @media (max-width: 1199px) {
                margin-bottom: 48px;
            }

            @media (max-width: 991px) {
                margin-bottom: rem(40);
            }

            @media (max-width: 600px) {
                margin-bottom: 25px;
            }

            &-text {
                font-size: 19px;
                line-height: 120%;
                color: #656669;
                margin-bottom: 20px;

                @media (max-width: 1699px) {
                }

                @media (max-width: 1199px) {
                    font-size: 17px;
                }

                @media (max-width: 991px) {
                }

                @media (max-width: 600px) {
                }
            }

            &-items {
                display: flex;
                align-items: center;
            }

            &-item {
                position: relative;
                width: 31px;
                font-size: 31px;
                margin-right: 27px;

                &:last-child {
                    margin-right: 0;
                }

                svg {
                    @include tr('all');
                    fill: #333333;
                    width: 100%;
                }

                &.hover, &.active {
                    svg {
                        fill: $red;
                    }
                }

                input {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                    cursor: pointer;
                }
            }
        }
    }

    &__columns {
        display: flex;
        margin: 0 -50px;

        @media (max-width: 1699px) {
            margin: 0 -25px;
        }

        @media (max-width: 991px) {
            flex-direction: column;
            margin: 0;
        }

        @media (max-width: 600px) {
        }
    }

    &__column {
        width: calc(33.3333% - 100px);
        margin: 0 50px;

        @media (max-width: 1699px) {
            width: calc(33.3333% - 5px);
            margin: 0 25px;
        }

        @media (max-width: 1199px) {
            width: calc(50% - 50px);
        }

        @media (max-width: 991px) {
            width: 100%;
            margin: 0;
            margin-bottom: 25px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        @media (max-width: 600px) {
        }

        &:last-child {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            width: calc(77.7777% - 100px);
            margin: 0 50px;

            @media (max-width: 1699px) {
                width: calc(77.7777% - 50px);
                margin: 0 25px;
            }

            @media (max-width: 1199px) {
                justify-content: flex-start;
                width: calc(50% - 50px);
            }

            @media (max-width: 991px) {
                width: 100%;
                margin: 0;
            }

            @media (max-width: 600px) {
            }
        }
    }

    .form {
        &__inputs {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-end;
            margin: 0 -50px;

            @media (max-width: 1699px) {
                margin: 0 -25px;
            }

            @media (max-width: 1199px) {
                flex-direction: column;
                margin: 0;
            }

            @media (max-width: 991px) {
                flex-direction: row;
                justify-content: space-between;
                margin: 0 -15px;
            }

            @media (max-width: 600px) {
                margin: 0;
            }
        }

        &__input, &__submit {
            width: calc(50% - 100px);
            margin: 0 50px;
            margin-bottom: 34px;

            @media (max-width: 1699px) {
                width: calc(50% - 50px);
                margin: 0 25px;
                margin-bottom: 22px;
            }

            @media (max-width: 1199px) {
                width: 100%;
                margin: 0;
                margin-bottom: 37px;
            }

            @media (max-width: 991px) {
                width: calc(50% - 30px);
                margin: 0 15px;
                margin-bottom: 32px;
            }

            @media (max-width: 600px) {
                width: 100%;
                margin: 0;
                margin-bottom: 22px;
            }

            input {
                min-height: 48px;
            }

            &:nth-child(2) {
                @media (max-width: 1699px) {
                    margin-bottom: 37px;
                }

                @media (max-width: 991px) {
                    margin-bottom: 32px;
                }

                @media (max-width: 600px) {
                    margin-bottom: 22px;
                }
            }

            &:nth-child(3) {
                margin-bottom: 0;

                @media (max-width: 1699px) {
                    min-height: 95px;
                }

                @media (max-width: 1199px) {
                    margin-bottom: 34px;
                }

                @media (max-width: 991px) {
                    margin-bottom: 0;
                }

                @media (max-width: 600px) {
                    margin-bottom: 25px;
                }
            }
        }

        &__submit {
            margin: 0;
        }

        label {
            margin-bottom: 11px;

            @media (max-width: 1199px) {
                font-size: 17px;
            }
        }

        &__textarea {
            margin-bottom: 0;

            textarea {
                min-height: 164px;
            }
        }

        &__checkbox {
            margin-top: rem(40);

            @media (max-width: 1699px) {
                align-items: center;
                margin-top: 0;
                margin-bottom: 27px;
            }

            @media (max-width: 991px) {
                margin-bottom: 24px;
            }

            &-text {
                @media (max-width: 1699px) {
                    font-size: 12px;
                }

                @media (max-width: 600px) {
                    font-size: 10px;
                }
            }

            &-btn {
                @media (max-width: 600px) {
                    width: 15px;
                    height: 15px;
                    margin-right: 10px;
                }
            }
        }

        &__submit {
            margin-top: auto;
            margin-bottom: 0 !important;

            @media (max-width: 1699px) {
                display: flex;
                flex-direction: column;
            }

            .btn {
                width: 100%;
                max-width: 100%;
                min-height: 48px;
                font-size: 15px;
                line-height: 1;
                border-radius: 5px;
                background: #333333;
                padding: 10px 0;
            }
        }
    }
}
