.s-service-centres {
    overflow-x: visible;

    &__info {
        display: flex;
        margin-bottom: rem(20);
        flex-direction: column;
        gap: rem(20);

        @media (min-width: 768px) {
            margin-bottom: rem(44);
            gap: rem(40);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(48);
            gap: rem(35);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(24);
            flex-direction: row;
            gap: rem(120);
        }
    }

    &__title {
        font-size: rem(40);
        font-weight: 500;
        line-height: rem(40);
        color: #ceced0;
        letter-spacing: -0.03em;
        white-space: nowrap;

        @media (min-width: 1024px) {
            font-size: rem(45);
            line-height: rem(45);
        }

        @media (min-width: 1600px) {
            font-size: rem(70);
            line-height: rem(70);
        }

        @media (min-width: 1920px) {
            font-size: rem(80);
            line-height: rem(80);
        }
    }

    &__text {
        font-size: rem(13);
        line-height: rem(14);
        color: #ceced0;

        @media (min-width: 768px) {
            display: flex;
            align-items: flex-start;
            font-size: rem(15);
            line-height: rem(19);
        }

        @media (min-width: 1600px) {
            font-size: rem(19);
            line-height: rem(25);
            flex-direction: column;
        }

        p {
            @media (min-width: 768px) {
                width: 50%;
            }

            @media (min-width: 1600px) {
                width: 100%;
            }

            &:not(:last-child) {
                margin: 0 0 rem(15);

                @media (min-width: 1600px) {
                    margin: 0 0 rem(30);
                }
            }
        }
    }

    .l-default {
        position: relative;
        max-width: rem(1520);

        &:after {
            position: absolute;
            bottom: 0;
            left: calc(-100% + 90px);
            width: 100%;
            height: 1px;
            background: $red;
            content: "";

            @media screen and (max-width: 1699px) {
                left: calc(-100% + 70px);
            }

            @media screen and (max-width: 1023px) {
                left: calc(-100% + 30px);
            }

            @media screen and (max-width: 767px) {
                left: calc(-100% + 12px);
            }
        }

        &:before {
            z-index: 1;
            position: absolute;
            right: calc(-100% + 90px);
            bottom: 0;
            width: 100%;
            height: 1px;
            background: $red;
            content: "";

            @media screen and (max-width: 1699px) {
                right: calc(-100% + 70px);
            }

            @media screen and (max-width: 1023px) {
                right: calc(-100% + 30px);
            }

            @media screen and (max-width: 767px) {
                right: calc(-100% + 12px);
            }
        }
    }

    &__tabs {
        position: relative;
        display: flex;
        width: 100%;

        &:after {
            width: 100%;
            min-height: 100%;
            border-bottom: 1px solid $red;
            content: "";
        }
    }

    &__tab {
        padding: 12px 0;
        font-size: rem(13);
        font-weight: 400;
        line-height: 1.2;
        color: $grey;
        white-space: nowrap;
        border-bottom: 1px solid $red;
        box-sizing: border-box;

        @media (min-width: 768px) {
            min-width: rem(193);
            font-size: rem(15);
        }
        @media (min-width: 1024px) {
            min-width: rem(228);
            font-size: rem(19);
        }

        @media (min-width: 1600px) {
            min-width: rem(265);
            font-size: rem(25);
        }

        @media (min-width: 1920px) {
            font-size: rem(21);
        }

        &-title {
            padding: 0 rem(20);
            text-align: center;
            transition: all 0.8s ease-in-out;

            span {
                display: none;
                @media (min-width: 768px) {
                    display: inline-block;
                }
            }
        }
        &:last-child {
            border-right: none;
        }

        &:hover {
            color: $grey_light_more_2;
        }

        &.is-active {
            padding-bottom: 1px;
            color: $grey_light_more_2;
            border: rem(2) solid $red;
            border-bottom: none;
            box-sizing: border-box;

            @media (min-width: 768px) {
                width: auto;
            }

            .s-rulers__tab-title {
                display: flex;
                justify-content: center;
                border: none;

                span {
                    display: inline-block;
                    margin-right: 5px;
                }
            }
        }

        &-content {
            display: none;
            margin-top: rem(12);

            @media (min-width: 768px) {
                margin-top: rem(35);
            }

            &.is-active {
                display: block;
            }

            .l-default {
                @media (max-width: 767px) {
                    padding: 0;
                }
            }
        }
    }

    &__map {
        width: 100%;
        height: rem(650);
        filter: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'><filter id='grayscale'><feColorMatrix type='matrix' values='0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0'/></filter></svg>#grayscale");
        -webkit-filter: grayscale(100%);
        filter: grayscale(100%);

        @media (min-width: 768px) {
            height: rem(320);
        }

        @media (min-width: 1024px) {
            height: rem(400);
        }

        @media (min-width: 1600px) {
            height: rem(560);
        }
    }

    &__list {
        width: 100%;
        height: 100%;
        height: rem(650);
        padding: rem(30);
        background-image: url(../img/support/sc-list-bg.jpg);
        background-size: cover;

        @media (min-width: 768px) {
            height: rem(320);
            padding: rem(35) rem(55);
        }

        @media (min-width: 1024px) {
            height: rem(340);
            padding: rem(50) rem(70);
        }

        @media (min-width: 1600px) {
            height: rem(920);
            padding: rem(115) rem(95);
        }

        @media (min-width: 1920px) {
            height: rem(995);
            padding: rem(135) rem(150);
        }

        &-content {
            height: -webkit-fill-available;
            padding-right: rem(15);
            overflow-y: scroll;
        }
    }
}

.service-centre {
    &__items {
        display: grid;
        gap: rem(25) rem(80);
        grid-template-columns: repeat(1, 1fr);

        @media (min-width: 768px) {
            grid-template-columns: repeat(2, 1fr);
            gap: rem(45);
        }

        @media (min-width: 1024px) {
            gap: rem(80) rem(36);
        }

        @media (min-width: 1600px) {
            grid-template-columns: repeat(3, 1fr);
        }

        @media (min-width: 1920px) {
            gap: rem(80) rem(100);
        }

        &-group {
            margin-bottom: rem(60);

            @media (min-width: 1920px) {
                margin-bottom: rem(80);
            }
        }
    }

    &__item {
        position: relative;
        height: min-content;
        border-top: rem(2) solid #000203;

        &-ch {
            display: none;

            @media (min-width: 768px) {
                display: block;
                font-size: rem(60);
                font-weight: 500;
                line-height: 100%;
                color: #9a9ea9;
                letter-spacing: -0.03em;
            }

            @media (min-width: 1600px) {
                font-size: rem(80);
            }
        }

        &-city {
            display: flex;
            align-items: center;
            height: rem(38);
            margin-bottom: rem(5);
            font-size: rem(17);
            font-weight: 300;
            line-height: 115%;
            text-align: center;
            color: #ffffff;
            background: rgba(0, 2, 3, 0.3);
            letter-spacing: -0.03em;

            @media (min-width: 768px) {
                height: rem(42);
                margin-bottom: rem(8);
                font-size: rem(17);
                text-align: left;
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(15);
                font-size: rem(19);
            }

            @media (min-width: 1600px) {
                margin-bottom: rem(20);
            }
        }

        &-top {
            display: flex;
            justify-content: space-between;
            margin-bottom: rem(12);
            font-size: rem(12);
            font-weight: 300;
            line-height: 115%;
            flex-direction: column;
            gap: rem(15);
            letter-spacing: -0.03em;

            @media (min-width: 768px) {
                align-items: flex-start;
                margin-bottom: rem(10);
                flex-direction: column;
                gap: rem(6);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(19);
                font-size: rem(15);
                gap: rem(10);
            }

            @media (min-width: 1600px) {
                margin-bottom: rem(27);
                font-size: rem(17);
            }
        }

        &-name {
            color: #ffffff;

            @media (min-width: 1024px) {
                max-width: 70%;
                min-height: rem(40);
                font-size: rem(17);
            }
        }

        &-work {
            color: #9a9ea9;

            @media (min-width: 1024px) {
                font-size: rem(21);
            }
        }

        &-address {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: rem(13);
            font-size: rem(17);
            font-weight: 600;
            line-height: 115%;
            color: #ffffff;
            letter-spacing: -0.03em;

            @media (min-width: 768px) {
                margin-bottom: rem(14);
                font-size: rem(17);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(13);
                font-size: rem(19);
            }

            @media (min-width: 1600px) {
                margin-bottom: rem(22);
                font-size: rem(21);
            }
        }

        &-info {
            display: flex;
            align-items: center;
            margin-bottom: rem(25);
            font-size: rem(10);
            line-height: 115%;
            color: #c6c7d0;
            gap: rem(12);

            @media (min-width: 768px) {
                align-items: flex-start;
                font-size: rem(12);
                flex-direction: column;
            }

            @media (min-width: 1024px) {
                font-size: rem(15);
                gap: rem(12);
            }

            @media (min-width: 1600px) {
                font-size: rem(18);
                gap: rem(10);
            }
        }

        &-route {
            font-size: rem(12);
            font-weight: 300;
            line-height: 115%;
            color: #d50032;
            letter-spacing: -0.03em;

            @media (min-width: 1024px) {
                position: absolute;
                right: 0;
                bottom: rem(25);
            }

            @media (min-width: 1600px) {
                font-size: rem(15);
            }
        }
    }
}

.ymaps-2-1-79-balloon {
    &__content {
        margin-right: 0 !important;
        padding: 0 !important;

        ymaps {
            overflow: hidden !important;
            height: 100% !important;
        }
    }

    &__tail {
        display: none !important;
    }

    &__close {
        position: absolute !important;
        top: rem(17);
        right: rem(18);

        &-button {
            width: rem(20) !important;
            height: rem(20) !important;
            opacity: 1 !important;
            background-image: url("data:image/svg+xml,%3Csvg width='22' height='22' viewBox='0 0 22 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L21 21' stroke='%23656669' stroke-linecap='round'/%3E%3Cpath d='M21 1C21 1 17.1215 4.87848 14.6364 7.36364C9.31103 12.689 0.999999 21 0.999999 21' stroke='%23656669' stroke-linecap='round'/%3E%3C/svg%3E%0A") !important;
            background-repeat: no-repeat;
        }
    }
}

.map-ballon {
    width: rem(351);

    @media (min-width: 768px) {
        width: rem(353);
    }

    @media (min-width: 1024px) {
        width: rem(405);
    }

    &__city {
        display: flex;
        align-items: center;
        height: rem(55);
        padding: 0 rem(30);
        font-size: rem(15);
        font-weight: 600;
        line-height: 115%;
        text-transform: uppercase;
        color: #c6c7d0;
        background: rgba(0, 2, 3, 0.8);
        letter-spacing: -0.03em;

        @media (min-width: 1024px) {
            font-size: rem(18);
        }
    }

    &__info {
        width: 100%;
        height: 100%;
        padding: rem(17) rem(21) rem(17) rem(26);
        background-image: url("../img/support/bg_ballon.png");
        background-size: cover;
        background-repeat: no-repeat;
        letter-spacing: -0.03em;
    }

    &__name {
        width: rem(227);
        margin-bottom: rem(10);
        padding-bottom: rem(8);
        font-size: rem(13);
        font-weight: 300;
        line-height: 115%;
        color: #ffffff;
        border-bottom: rem(1) solid #d50032;

        @media (min-width: 1024px) {
            width: rem(300);
            margin-bottom: rem(15);
            font-size: rem(15);
        }
    }

    &__work {
        margin-bottom: rem(12);
        font-size: rem(13);
        font-weight: 300;
        line-height: 115%;
        color: #000203;

        @media (min-width: 1024px) {
            margin-bottom: rem(20);
            font-size: rem(15);
        }
    }

    &__address {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: rem(12);
        font-size: rem(17);
        font-weight: 600;
        line-height: 115%;
        color: #ffffff;

        @media (min-width: 1024px) {
            font-size: rem(19);
        }
    }

    &__phone {
        margin-bottom: rem(12);
        font-size: rem(13);
        font-weight: 400;
        line-height: 115%;
        color: #c6c7d0;

        @media (min-width: 1024px) {
            font-size: rem(15);
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: rem(13);
        font-weight: 400;
        line-height: 115%;
        color: #c6c7d0;

        @media (min-width: 1024px) {
            font-size: rem(15);
        }
    }
}
